.header__title {
    flex: 1 1 100%;
    width: 100%;
    text-align: center;
    font-size: 7vw;
    font-weight: bold;
    text-shadow: 0px 0px 2px rgba(black, 0.4);
    margin: 25px auto;
    line-height: 1.3em;
    display: flex;
    white-space: pre;
}

@media screen and (min-width: 1100px) {
    .header__title {
        font-size: 100px;
    }
}

.slider {
    display: inline-block;
    height: 1.3em;

    overflow: hidden;

    vertical-align: middle;

    mask-image: linear-gradient(transparent, white, white, white, transparent);
    mask-type: luminance;
    mask-mode: alpha;
}
.slider__word {
    display: block;
    line-height: 1.3em;
    text-align: left;
}
.slider__words {
    display: inline-block;
    margin: 0;
    padding: 0;
    list-style: none;
    color: purple;
    animation-name: wordSlider;
    animation-timing-function: ease-out;
    animation-iteration-count: infinite;
    animation-duration: 10s;
}

.videoWrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
}
.videoWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@keyframes wordSlider {
    $steps: -0%, -20%, -40%, -60%, -80%;

    @for $index from 0 to length($steps)-1 {
        $next: $index + 1;
        $step: floor(100/ (length($steps)-1));
        $animation_step: floor($step * 0.2);

        #{$step*$index}%,
        #{($step*$next) - $animation_step}% {
            transform: translateY(nth($steps, $index + 1));
        }
    }
    100% {
        transform: translateY(nth($steps, length($steps)));
    }
}
